<template>
  
  <div class="flex justify-center items-center flex-col w-11/12 lg:w-5/12 mx-auto">
    <section class="flex py-5 items-center">
    <span class="font-bold text-2xl border-r-2 border-blue-400 pr-4">{{changeMonth()}}</span>
    <span class="font-bold text-lg pl-4">{{currentYear}}</span>
    </section>

    <section class="flex items-center justify-around w-full py-4 bg-blue-400 border-b">
        <button class="text-blue-100 text-xl" @click="prev()"><i class="fas fa-chevron-left"></i></button>
        <h1 class="text-white font-bold">My Calendar</h1>
        <button class="text-blue-100 text-xl" @click="next()"><i class="fas fa-chevron-right"></i></button>
    </section>

    <section class="flex w-full">
        <p 
        class="text-center text-sm md:text-base text-white border bg-blue-400 font-bold py-2" 
        style="width: 14.28%" 
        v-for="day in weekDays"
        :class="highlightWeekDay(day)" 
        :key="day">{{day}}
        </p>
    </section>

    <section class="flex flex-wrap w-full" style="height: 250px">
        <p 
        class="py-2 bg-blue-200 border"
        style="width: 14.28%" 
        v-for="freeSpaceNum in startingDay()" 
        :key="freeSpaceNum">
        </p>
        <p 
        class="flex items-center justify-center text-center text-sm md:text-base py-2 bg-blue-300 border text-white font-bold"
        style="width: 14.28%" 
        :class="highlightDay(num)"
        v-for="num in daysOfMonth()" 
        :key="num">{{num}}
        </p>
    </section>

  </div>
</template>

<script>

export default {
    methods:{
        changeMonth(){
        return new Date(this.currentYear, this.currentMonth).toLocaleString("default", {month: "long"});
        },
        daysOfMonth(){
            return new Date(this.currentYear, this.currentMonth + 1, 0).getDate();
        },
        startingDay(){
            return new Date(this.currentYear, this.currentMonth, 1).getDay();
        },
        next(){
            if(this.currentMonth == 11){
                this.currentYear++;
                this.currentMonth = 0;
            }else{
               this.currentMonth++;
            }
        },
        prev(){
             if(this.currentMonth == 0){
                this.currentYear--;
                this.currentMonth = 11;
            }else{
               this.currentMonth--; 
            }
        },
         highlightDay(num){
            const calendarDateClass = new Date(this.currentYear, this.currentMonth, num).toDateString();
            const currentDateClass = new Date().toDateString();
            return calendarDateClass == currentDateClass ? "bg-blue-400" : "";
        },
        highlightWeekDay(day){
            return day == "Sun" || day == "Fri" || day=="Sat" ? "bg-blue-600" : "";
        }
    },
    data(){
        return{
            currentYear: new Date().getUTCFullYear(),
            currentMonth: new Date().getMonth(),
            today: new Date().getDate(),
            weekDays:["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"]
        }
    }
}
</script>